* {
    box-sizing: border-box;
}
body {
    display: flex;
    flex-direction: column;
    gap: 35px;
    padding: 5px;
    padding-top: 60px;
    width: 100%;
    height: 100vh;
    justify-content: start;
    align-items: center;
    background-color: rgba(32, 33, 36, 0.9);
}

#container {
    display: grid;
    grid-template-columns: repeat(5, 100px);
    grid-template-rows: repeat(5, 100px);
    border: 1px solid black;
    gap: 2px;
    background-color: rgb(166, 163, 163);
    transform: rotateX(51deg) rotateZ(43deg);
    transform-style: preserve-3d;
    box-shadow: 1px 1px 0px 1px #3c354a, 2px 2px 0px 1px #3c354a, 3px 3px 0px 1px #3c354a, 4px 4px 0px 1px #3c354a,
        5px 5px 0px 1px #3c354a, 6px 6px 0px 1px #3c354a, 7px 7px 0px 1px #3c354a, 8px 8px 0px 1px #3c354a,
        9px 9px 0px 1px #3c354a, 7px 7px 0px 1px #3c354a, -1px 0 28px 0 rgba(219, 219, 231, 0.01),
        28px 28px 28px 15px rgba(215, 215, 246, 0.25);
    transition: 0.4s ease-in-out transform, 0.4s ease-in-out box-shadow;
}

.square:hover {
    transform: translate3d(0px, 0px, 10px);
    box-shadow: 0 0 10px black;
}

.square {
    background-color: rgba(240, 248, 255);
    text-align: center;
    height: 100%;
    width: 100%;
    font-size: 20px;
    transition: 0.3s ease-in-out transform, 0.3s ease-in-out box-shadow;
    transform-style: preserve-3d;
}

.empty {
    background-color: white;
}
.weak {
    background-image: url("./assets/darkHead-transform.png");
    background-repeat: no-repeat;
    background-size: cover;
}
.strong {
    background-image: url("./assets/redHead-transform.png");
    background-repeat: no-repeat;
    background-size: cover;
}

.top-req {
    box-shadow: 0 0 20px blue, 0 0 40px blue inset;
    isolation: isolate;
}

.mid-req {
    box-shadow: 0 0 20px rgb(49, 90, 255), 0 0 50px rgb(78, 114, 255) inset;
    isolation: isolate;
}

.low-req {
    box-shadow: 0 0 20px rgba(50, 202, 249), 0 0 50px rgb(155, 160, 255) inset;
    isolation: isolate;
}

#buttons {
    display: flex;
    gap: 50px;
    width: 500px;
    justify-content: space-evenly;
    align-items: center;
}

#inputs {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 30px;
}

label {
    color: white;
}

/** BUTTON */
.button {
    align-items: center;
    background-color: initial;
    background-image: linear-gradient(#464d55, #25292e);
    border-radius: 8px;
    border-width: 0;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1), 0 3px 6px rgba(0, 0, 0, 0.05);
    box-sizing: border-box;
    color: #fff;
    cursor: pointer;
    display: inline-flex;
    flex-direction: column;
    font-family: expo-brand-demi, system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue",
        Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
    font-size: 18px;
    height: 52px;
    justify-content: center;
    line-height: 1;
    margin: 0;
    outline: none;
    overflow: hidden;
    padding: 0 32px;
    text-align: center;
    text-decoration: none;
    transform: translate3d(0, 0, 0);
    transition: all 150ms;
    vertical-align: baseline;
    white-space: nowrap;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
}

.button:hover {
    box-shadow: rgba(0, 1, 0, 0.2) 0 2px 8px;
    opacity: 0.85;
}

.button:active {
    outline: 0;
}

.button:focus {
    box-shadow: rgba(0, 0, 0, 0.5) 0 0 0 3px;
}

@media (max-width: 420px) {
    .button {
        height: 48px;
    }
}

/** CHECKBOX */
.switch {
    position: relative;
    display: inline-block;
}
.switch-input {
    display: none;
}
.switch-label {
    display: block;
    width: 48px;
    height: 24px;
    text-indent: -150%;
    clip: rect(0 0 0 0);
    color: transparent;
    user-select: none;
}
.switch-label::before,
.switch-label::after {
    content: "";
    display: block;
    position: absolute;
    cursor: pointer;
}
.switch-label::before {
    width: 100%;
    height: 100%;
    background-color: gray;
    border-radius: 9999em;
    -webkit-transition: background-color 0.25s ease;
    transition: background-color 0.25s ease;
}
.switch-label::after {
    top: 0;
    left: 0;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    background-color: #fff;
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.45);
    -webkit-transition: left 0.25s ease;
    transition: left 0.25s ease;
}

.switch-input:checked + .switch-label::before {
    background-color: #89c12d;
}
.switch-input:checked + .switch-label::after {
    left: 24px;
}

#sign {
    position: fixed;
    right: 20px;
    bottom: 20px;
    width: 200px;
    height: 20px;
    padding: 2px;
    font-size: 14px;
    text-align: end;
    color: white;
}

#language-container {
    position: fixed;
    top: 20px;
    right: 20px;
    display: flex;
    gap: 10px;
    align-items: center;
}

#switch-2:checked + .switch-label::before {
    background-color: rgba(32, 33, 36, 0.9);
}

#language-label::before {
    background-color: rgba(32, 33, 36, 0.9);
}
#language-label::after {
    background-color: #fff;
}

/** Toastr Notiification */

/* The snackbar - position it at the bottom and in the middle of the screen */
#tip {
    visibility: hidden; /* Hidden by default. Visible on click */
    background-color: transparent; /* Black background color */
    color: red; /* White text color */
    font-size: 36px;
    font-weight: 700;
    text-align: center; /* Centered text */
}

/* Show the snackbar when clicking on a button (class added with JavaScript) */
#tip.show {
    visibility: visible; /* Show the snackbar */
    /* Add animation: Take 0.5 seconds to fade in and out the snackbar.
    However, delay the fade out process for 2.5 seconds */
    -webkit-animation: blink5 2s linear infinite;
    animation: blink5 2s linear infinite;
}
@-webkit-keyframes blink5 {
    0% {
        color: rgb(128, 0, 0);
    }
    50% {
        color: rgb(183, 65, 14);
    }
    100% {
        color: rgb(128, 0, 0);
    }
}
@keyframes blink5 {
    0% {
        color: rgb(128, 0, 0);
    }
    50% {
        color: rgb(183, 65, 14);
    }
    100% {
        color: rgb(128, 0, 0);
    }
}
