* {
  box-sizing: border-box;
}

body {
  width: 100%;
  height: 100vh;
  background-color: #202124e6;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  gap: 35px;
  padding: 60px 5px 5px;
  display: flex;
}

#container {
  transform-style: preserve-3d;
  background-color: #a6a3a3;
  border: 1px solid #000;
  grid-template-rows: repeat(5, 100px);
  grid-template-columns: repeat(5, 100px);
  gap: 2px;
  transition: transform .4s ease-in-out, box-shadow .4s ease-in-out;
  display: grid;
  transform: rotateX(51deg)rotateZ(43deg);
  box-shadow: 1px 1px 0 1px #3c354a, 2px 2px 0 1px #3c354a, 3px 3px 0 1px #3c354a, 4px 4px 0 1px #3c354a, 5px 5px 0 1px #3c354a, 6px 6px 0 1px #3c354a, 7px 7px 0 1px #3c354a, 8px 8px 0 1px #3c354a, 9px 9px 0 1px #3c354a, 7px 7px 0 1px #3c354a, -1px 0 28px #dbdbe703, 28px 28px 28px 15px #d7d7f640;
}

.square:hover {
  transform: translate3d(0, 0, 10px);
  box-shadow: 0 0 10px #000;
}

.square {
  text-align: center;
  height: 100%;
  width: 100%;
  transform-style: preserve-3d;
  background-color: #f0f8ff;
  font-size: 20px;
  transition: transform .3s ease-in-out, box-shadow .3s ease-in-out;
}

.empty {
  background-color: #fff;
}

.weak {
  background-image: url("darkHead-transform.9e8828d0.png");
  background-repeat: no-repeat;
  background-size: cover;
}

.strong {
  background-image: url("redHead-transform.c5ca068d.png");
  background-repeat: no-repeat;
  background-size: cover;
}

.top-req {
  isolation: isolate;
  box-shadow: 0 0 20px #00f, inset 0 0 40px #00f;
}

.mid-req {
  isolation: isolate;
  box-shadow: 0 0 20px #315aff, inset 0 0 50px #4e72ff;
}

.low-req {
  isolation: isolate;
  box-shadow: 0 0 20px #32caf9, inset 0 0 50px #9ba0ff;
}

#buttons {
  width: 500px;
  justify-content: space-evenly;
  align-items: center;
  gap: 50px;
  display: flex;
}

#inputs {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
  display: flex;
}

label {
  color: #fff;
}

.button {
  background-color: initial;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  height: 52px;
  text-align: center;
  vertical-align: baseline;
  white-space: nowrap;
  -webkit-user-select: none;
  user-select: none;
  touch-action: manipulation;
  background-image: linear-gradient(#464d55, #25292e);
  border-width: 0;
  border-radius: 8px;
  outline: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0 32px;
  font-family: expo-brand-demi, system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica Neue, Arial, Noto Sans, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji;
  font-size: 18px;
  line-height: 1;
  text-decoration: none;
  transition: all .15s;
  display: inline-flex;
  overflow: hidden;
  transform: translate3d(0, 0, 0);
  box-shadow: 0 10px 20px #0000001a, 0 3px 6px #0000000d;
}

.button:hover {
  opacity: .85;
  box-shadow: 0 2px 8px #00010033;
}

.button:active {
  outline: 0;
}

.button:focus {
  box-shadow: 0 0 0 3px #00000080;
}

@media (max-width: 420px) {
  .button {
    height: 48px;
  }
}

.switch {
  display: inline-block;
  position: relative;
}

.switch-input {
  display: none;
}

.switch-label {
  width: 48px;
  height: 24px;
  text-indent: -150%;
  clip: rect(0 0 0 0);
  color: #0000;
  -webkit-user-select: none;
  user-select: none;
  display: block;
}

.switch-label:before, .switch-label:after {
  content: "";
  cursor: pointer;
  display: block;
  position: absolute;
}

.switch-label:before {
  width: 100%;
  height: 100%;
  background-color: gray;
  border-radius: 9999em;
  transition: background-color .25s;
}

.switch-label:after {
  width: 24px;
  height: 24px;
  background-color: #fff;
  border-radius: 50%;
  transition: left .25s;
  top: 0;
  left: 0;
  box-shadow: 0 0 2px #00000073;
}

.switch-input:checked + .switch-label:before {
  background-color: #89c12d;
}

.switch-input:checked + .switch-label:after {
  left: 24px;
}

#sign {
  width: 200px;
  height: 20px;
  text-align: end;
  color: #fff;
  padding: 2px;
  font-size: 14px;
  position: fixed;
  bottom: 20px;
  right: 20px;
}

#language-container {
  align-items: center;
  gap: 10px;
  display: flex;
  position: fixed;
  top: 20px;
  right: 20px;
}

#switch-2:checked + .switch-label:before, #language-label:before {
  background-color: #202124e6;
}

#language-label:after {
  background-color: #fff;
}

#tip {
  visibility: hidden;
  color: red;
  text-align: center;
  background-color: #0000;
  font-size: 36px;
  font-weight: 700;
}

#tip.show {
  visibility: visible;
  animation: blink5 2s linear infinite;
}

@keyframes blink5 {
  0% {
    color: maroon;
  }

  50% {
    color: #b7410e;
  }

  100% {
    color: maroon;
  }
}

